
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
name: "getGoods",
components: {
ViewBox,
XHeader,
Tab,
TabItem,
Swiper,
SwiperItem,
Cell,
Group,
XInput,
PopupPicker,
XButton,
Box,
XAddress,
ChinaAddressV4Data,
Checker,
CheckerItem,
XDialog
},
created() {
this.queryOrder();
if (JSON.stringify(this.$route.params) === "{}") {
this.queryUserAddress();
} else {
this.getGoodsObj.addressId = this.$route.params.addressId;
this.getGoodsObj.addressName = this.$route.params.addressName;
}
this.queryUser();
},
computed: {
payAmount: function () {
return this.getGoodsObj.quantity * this.price;
}
},
data() {
return {
index: 0,
checkerWhich: 1,
price: 1,
userInfo: {},
productInfo: {},
getGoodsObj: {
quantity: "",
addressId: "",
proxyProvice: "",
proxyCity: "",
proxyArea: "",
addressName: "",
addressValue: []
},
orderList: [],
addressData: ChinaAddressV4Data,
isInpNum: false,
showToast: false
};
},
methods: {
queryUser() {
this.$api
.queryUser({})
.then(data => {
if (data.code === 200) {
this.userInfo = data.data.user;
this.queryProduct();
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryProduct() {
this.$api.queryProduct().then(data => {
if (data.code === 200) {
this.productInfo = data.data.product;
switch(this.userInfo.vipLevel)
{
case 0: // 分享大使
this.price = this.productInfo.price * 0.7
break;
case 1: // 经销商
this.price = this.productInfo.price * 0.65
break;
case 2: // 区县合伙人
this.price = this.productInfo.price * 0.6
break;
case 3: // 城市合伙人
this.price = this.productInfo.price * 0.55
break;
case 4: // 省级合伙人
this.price = this.productInfo.price * 0.5
break;
default: // 普通用户
this.price = this.productInfo.price
}
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryUserAddress() {
this.$api.queryUserAddress({}).then(data => {
if (data.code === 200) {
if (data.data.address.length < 1) {
this.getGoodsObj.addressName = '无'
} else {
this.getGoodsObj.addressId = data.data.address[0].id;
this.getGoodsObj.addressName =
data.data.address[0].province +
data.data.address[0].city +
data.data.address[0].area +
data.data.address[0].detail;
}
}
});
},
queryOrder() {
this.$vux.loading.show({
text: ''
});
this.$api
.queryOrder({
pageNo: 1,
pageSize: 500
})
.then(data => {
if (data.code === 200) {
this.orderList = data.data.items;
}
this.$vux.loading.hide();
});
},
cancelOrder(id) {
this.$api
.cancelOrder({
id: id
})
.then(data => {
if (data.code === 200) {
this.$vux.toast.text("取消订单成功", "top");
this.queryOrder();
}
});
},
insertOrder(isOffline) {
let insertObj = {};
if (!this.getGoodsObj.addressId) {
return this.$vux.toast.text("请添加地址后再拿货~", "top");
}
insertObj.addressId = this.getGoodsObj.addressId;
insertObj.productId = this.productInfo.productId;
insertObj.quantity = this.getGoodsObj.quantity;
insertObj.isOffline = isOffline;
this.$api.insertOrder(insertObj).then(data => {
if (data.code === 200) {
if (isOffline === 0) {
this.onBridgeReady(data.data.appId, data.data.nonceStr, data.data.package, data.data.paySign, data.data.timeStamp)
} else {
this.$vux.toast.text("添加线下支付订单成功~", "top");
this.queryOrder();
this.index = 1;
}
this.showToast = false;
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
toSelectAddress() {
this.$router.push({
name: "selectAddress",
params: {
addressId: this.getGoodsObj.addressId,
addressName: this.getGoodsObj.addressName
}
});
},
toSale() {
this.$router.push({
name: "sales"
});
},
selectChecker(val) {
if (val === 1) {
this.queryOrder();
}
},
selectCheckerBtn(val) {
if (val == '1') {
if (this.userInfo.vipLevel === -1) {
this.getGoodsObj.quantity = 1;
this.isInpNum = true;
}
} else {
this.isInpNum = false;
}
},
onBridgeReady(appId, nonceStr, wxPackage, paySign, timeStamp){
WeixinJSBridge.invoke(
'getBrandWCPayRequest', {
"appId": appId,     //公众号名称，由商户传入
"timeStamp": timeStamp.toString(),         //时间戳，自1970年以来的秒数
"nonceStr": nonceStr, //随机串
"package": wxPackage,
"signType": "MD5",         //微信签名方式：
"paySign": paySign //微信签名
},
function(res){

if(res.err_msg == "get_brand_wcpay_request:ok" ){


this.$vux.toast.text("支付成功", "top");
this.queryOrder();
this.index = 1;
} else {
this.$vux.toast.text("支付失败", "top");
}
}
);
},
whichStatus(order) {
if (order.orderStatus == 0) {
if (order.isOffline == 1) {
return "待公司确认"
} else {
return "未支付";
}
} else if (order.orderStatus == 1) {
return "待发货";
} else if (order.orderStatus == 2) {
return "已发货";
}
}
}
};



